/* global.css */
body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    color: #333;
    text-align: center;
    padding-top: 50px;
}

h1 {
    font-size: 2rem;
    color: #002855;
    margin-bottom: 30px;
    margin-top: 90px;
}

button {
    padding: 10px;
    font-size: 1.2rem;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}

input, select {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
}

.table-container {
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1200px;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
}

th, td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

th {
    background-color: #007bff;
    color: #fff;
}

.cikis {
    position: absolute;
    top: 10px;
    right: 20px;
    background-color: red;
    color: white;
    padding: 15px 30px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: bold;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s ease;
}

.cikis:hover {
    background-color: #b30000;
}

.yardimSecim {
    position: absolute;
    top: 10px;
    right: 100px;
    background-color: white;
    color: green;
    border: 2px solid #4CAF50;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
}

.yardimSecim:hover {
    background-color: #e7f4e1;
    border-color: #45a049;
    color: #45a049;
}

.menu {
    position: relative;
    margin-right: 20px;
    cursor: pointer;
}

.dropdown {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #002D6B;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 10;
    padding: 10px 0;
}

.dropdown li {
    color: white;
    padding: 8px 16px;
    list-style: none;
    cursor: pointer;
}

.dropdown li:hover {
    background-color: #004080;
}

.menu:hover .dropdown {
    display: block;
}

.main-content {
    padding: 20px;
    margin: 0 auto;
    max-width: 1200px;
}

/* Navbar Stilleri */
.navbar {
    background-color: #002855;
    color: #fff;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 30px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
    z-index: 100;
}

.logo {
    height: 50px;
    margin-right: 20px;
    border-radius: 5px;
    background-color: #fff;
}

.ust {
    font-size: 16px;
    color: #fff;
    font-weight: bold;
    padding: 10px;
}

.link {
    text-decoration: none;
    color: inherit;
    display: block;
    padding: 10px 15px;
}

.link:hover {
    background-color: #696476;
}
