body {
    margin: 0;
    font-family: Arial, sans-serif;
    padding: 0;
  }
  
  h1 {
    font-size: 2.5rem;
    color: #002855;
    margin-bottom: 30px;
    margin-top: 90px;
    text-align: center;
  }
  
  .form-container {
    display: flex;
    justify-content: space-around;
    gap: 50px;
    flex-wrap: wrap;
    padding: 0 20px;
    margin: 0 auto;
    max-width: 1200px;
  }
  
  .form-column {
    flex: 1;
    min-width: 300px;
  }
  
  label {
    display: block;
    text-align: left;
    margin-bottom: 5px;
    font-size: 1rem;
    font-weight: bold;
  }
  
  input, select {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
  }
  
  .table-container {
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1200px;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
  }
  
  th, td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  th {
    background-color: #007bff;
    color: #fff;
  }
  
  button {
    padding: 10px;
    font-size: 1.2rem;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .cikis {
    position: absolute;
    top: 10px;
    right: 20px;
    background-color: red;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .cikis:hover {
    background-color: #b30000;
  }
  
  .yardimSecim {
    position: absolute;
    top: 10px;
    right: 100px;
    background-color: white;
    color: green;
    border: 2px solid #4CAF50;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .yardimSecim:hover {
    background-color: #e7f4e1;
    border-color: #45a049;
    color: #45a049;
  }
  
  .menu {
    position: relative;
    margin-right: 20px;
    cursor: pointer;
  }
  
  .dropdown {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #002D6B;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 10;
    padding: 10px 0;
  }
  
  .dropdown li {
    color: white;
    padding: 8px 16px;
    list-style: none;
    cursor: pointer;
  }
  
  .dropdown li:hover {
    background-color: #004080;
  }
  
  .menu:hover .dropdown {
    display: block;
  }
  
  .main-content {
    padding: 20px;
    margin: 0 auto;
    max-width: 1200px;
  }