body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #f4f4f4; /* Arka planı tamamen sade yap */
    color: #333;
    text-align: center;
    padding-top: 50px;
}

h1 {
    font-size: 2rem;
}

p {
    font-size: 40px;
}

.dashboard {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    min-height: 100vh;
    padding-top: 70px; /* Navbar'ın altına içerik yerleştirmek için */
}

.navbar {
    background-color: #002855;
    color: #fff;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 30px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
    z-index: 100;
}

.logo {
    height: 50px;
    margin-right: 20px;
    border-radius: 5px;
    background-color: #fff;
}

.menu {
    position: relative;
    margin-right: 20px;
    cursor: pointer;
}

.ust {
    font-size: 16px;
    color: #fff;
    font-weight: bold;
    padding: 10px;
}

.dropdown {
    display: none;
    position: absolute;
    background-color: #031427;
    color: #fff;
    top: 100%;
    left: 0;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
    list-style: none;
    padding: 0;
    margin: 0;
    border-radius: 5px;
    min-width: 200px;
    z-index: 10;
}

.dropdown.show {
    display: block;
}

.link {
    text-decoration: none;
    color: inherit;
    display: block;
    padding: 10px 15px;
}

.link:hover {
    background-color: #696476;
}

.dashboardContent {
    text-align: center;
    padding: 20px;
}

.username {
    color: #007bff;
}

.button {
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}